@import './utils/ResponsiveUtils.scss';

.header {
    display: flex;
    height: 40px;

    .closed-menu {
        position: absolute;
        top: 40px;
        right: 40px;

        .MuiIconButton-root {
            &:hover {
                .MuiSvgIcon-root {
                    color: #000;
                }
            }
        }
    }

    .opened-menu {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
    }

    .back-button {
        position: absolute;
        top: 40px;
        left: 40px;

        .MuiIconButton-root {
            &:hover {
                .MuiSvgIcon-root {
                    color: #000;
                }
            }
        }
    }
}

